	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya-gatsby/content/posts/patterned-triangle/index_en.md absPath of file >>> MarkdownRemark","html":"<p>As you might know, I recently run the <a href=\"https://github.com/toivonen/bem-love\">BEM\nLOVE</a> project to demonstrate how blocks\ncan communicate with JavaScript.<excerpt/></p>\n<p>The first example is dull enough; <a href=\"http://toivonen.github.com/bem-love/desktop.bundles/ask-her-what-music-she-likes/ask-her-what-music-she-likes.html\">a boy, a girl and the heartless\nworld</a>.</p>\n<p>Today, I was going to start with another one with a boy plunging into\ndifficulties of choice. He has <a href=\"http://toivonen.github.com/bem-love/desktop.bundles/choose-the-one/choose-the-one.html\">two girls to\nask</a>.\nSuddenly there is a <strong>challenge for you</strong>.</p>\n<p>Let’s think if it’s possible to attract a boy with a booooooring one-colour\ndress? Seems like no.<br/>\nSo, the girls in the page are in need of nice patterned dresses. Any ideas on\nhow to code a patterned triangle in CSS?</p>\n<h3>Where to code?</h3>\n<p>To dress the girls excellenty and toppingly you need just three steps:</p>\n<ul>\n<li>Fork the project and <a href=\"https://github.com/toivonen/bem-love#how-to-contribute\">run the\nserver</a>.</li>\n<li><a href=\"https://github.com/toivonen/bem-love/tree/master/desktop.bundles/choose-the-one/blocks/girl/_dressed\">Contribute to modifiers</a></li>\n<li>Make your pull request</li>\n</ul>","fields":{"slug":"en/posts/patterned-triangle/","prefix":"","disqusIdentifier":"en-posts-patterned-triangle"},"frontmatter":{"title":"Patterned triangles with  CSS","date":"19 March 2013","v2":null,"old":true,"tumblr":null,"meta":null,"cover":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya-gatsby/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"en/posts/patterned-triangle/","prev":{"id":"/Users/varya/WebDev/Personal/varya-gatsby/content/posts/run-app-in-time-on-mac/index_en.md absPath of file >>> MarkdownRemark","fields":{"slug":"en/posts/run-app-in-time-on-mac/","prefix":"","lang":"en","disqusIdentifier":"posts-run-app-in-time-on-mac-index-en"},"frontmatter":{"title":"Run on time (about Mac applications)"}},"next":{"id":"/Users/varya/WebDev/Personal/varya-gatsby/content/posts/frontend-babel-alpha/index_en.md absPath of file >>> MarkdownRemark","fields":{"slug":"en/posts/frontend-babel-alpha/","prefix":"","lang":"en","disqusIdentifier":"posts-frontend-babel-alpha-index-en"},"frontmatter":{"title":"The so different frontend community and how we deal with it"}}}}

